'use strict';

var React = require('react');

var TabbedArea = require('react-bootstrap/lib/TabbedArea');
var TabPane = require('react-bootstrap/lib/TabPane');

var Filter=require('./filter.jsx')

module.exports  = React.createClass({displayName: "exports",

  render: function() {
    return (
      React.createElement("div", {className: "filter-type-wrapper"}, 
        React.createElement(TabbedArea, {className: "filter-type-label", defaultActiveKey: 1}, 
          React.createElement(TabPane, {className: "", eventKey: 1, tab: "Basic Filters"}, 
                 React.createElement(Filter, {type: "basic"})
          ), 
          React.createElement(TabPane, {className: "", eventKey: 2, tab: "Advanced Filters"}, 
              React.createElement(Filter, {type: "advanced"})
          )
        )
      )
      );
  }

});
